@use "../variables";

pre.nfo {
    color: variables.$black;
    user-select: none;
    margin-bottom: 0;
    line-height: normal;
    font-size: 1.8vw;
    border-radius: 0.25rem;
    padding: 0 0.1rem;
    overflow: hidden;

    @media (min-width: 1200px) {
        font-size: 0.9vmax;
    }

    @media (prefers-color-scheme: dark) {
        color: variables.$hwite;
    }
}

@media (prefers-color-scheme: dark) {
    @keyframes fadeIn {
        0% {
            filter: drop-shadow(0px 0px 10px rgba(0, 0, 0, 0));
        }
        100% {
            filter: drop-shadow(0px 0px 10px variables.$blue);
        }
    }

    @keyframes darkCool {
        0% {
            filter: drop-shadow(0px 0px 10px variables.$orange);
        }
        33% {
            filter: drop-shadow(0px 0px 10px variables.$blue);
        }
        67% {
            filter: drop-shadow(0px 0px 10px variables.$awcy-red);
        }
        100% {
            filter: drop-shadow(0px 0px 10px variables.$orange);
        }
    }

    /* prettier-ignore */
    div#p-content0-0-0,
    div#p-content1-0-0 {
        animation:
            variables.$darkCoolDuration darkCool variables.$darkCoolDelay forwards infinite,
            calc(variables.$darkCoolDuration / 3) fadeIn variables.$darkCoolDelay;
    }
}
